import React, { useCallback, useState } from "react";
import {
  Icon,
  Col,
  Button,
  Popconfirm,
  Input,
  Tooltip,
  InputNumber
} from "antd";
import Style from "./style.module.css";
import { useDispatch } from "react-redux";

import {
  addMember,
  addActivity,
  addSprint,
  addMilestone
} from "../../../action/project";

const AddButtons = ({ projectId }) => {
  const dispatch = useDispatch();
  const [email, setUserEmail] = useState("");
  const [activityName, setActivityName] = useState("");
  const [sprintName, setSprintName] = useState("");
  const [milestoneDes, setMilestoneDes] = useState("");
  const [howManyDurations, setHowManyduration] = useState(1);
  const addMemberCallback = useCallback(
    () => dispatch(addMember({ email, projectId })),
    [dispatch, projectId, email]
  );

  const addActivityCallback = useCallback(
    () => dispatch(addActivity({ activityName, projectId })),
    [activityName, dispatch, projectId]
  );

  const addSprintCallback = useCallback(
    () => dispatch(addSprint({ sprintName, projectId })),
    [dispatch, projectId, sprintName]
  );

  const addMilestoneCallback = useCallback(
    () =>
      dispatch(
        addMilestone({ howManyDurations, projectId, description: milestoneDes })
      ),
    [dispatch, projectId, howManyDurations, milestoneDes]
  );

  return (
    <Col className={Style.button_flex} xs={24}>
      <Popconfirm
        icon={<Icon style={{ marginTop: "4px" }} type="plus-circle" />}
        title={
          <Input
            value={email}
            onChange={e => setUserEmail(e.target.value)}
            placeholder="Enter your member userId name"
            suffix={
              <Tooltip title="to add member to project">
                <Icon type="info-circle" style={{ color: "rgba(0,0,0,.45)" }} />
              </Tooltip>
            }
          />
        }
        placement="bottom"
        onConfirm={addMemberCallback}
        onCancel={console.log}
        okText="add to"
        cancelText=""
      >
        <Button className={Style.button}>add member </Button>
      </Popconfirm>
      <Popconfirm
        icon={<Icon style={{ marginTop: "4px" }} type="plus-circle" />}
        title={
          <Input
            onChange={e => setSprintName(e.target.value)}
            placeholder="Enter your activity name"
            suffix={
              <Tooltip title="to add activity to your project">
                <Icon type="info-circle" style={{ color: "rgba(0,0,0,.45)" }} />
              </Tooltip>
            }
          />
        }
        placement="bottom"
        onConfirm={() => {
          if (sprintName !== "") {
            addSprintCallback();
          }
        }}
        onCancel={console.log}
        okText="add to"
        cancelText=""
      >
        <Button className={Style.button}>add Sprint</Button>
      </Popconfirm>
      <Popconfirm
        icon={<Icon style={{ marginTop: "4px" }} type="plus-circle" />}
        title={
          <Input
            placeholder="Enter your activity name"
            onChange={e => setActivityName(e.target.value)}
            suffix={
              <Tooltip title="to add activity to your project">
                <Icon type="info-circle" style={{ color: "rgba(0,0,0,.45)" }} />
              </Tooltip>
            }
          />
        }
        placement="bottom"
        onConfirm={() => {
          if (activityName !== "") {
            addActivityCallback();
          }
        }}
        onCancel={console.log}
        okText="create activity"
        cancelText="no"
      >
        <Button className={Style.button}>add Activity</Button>
      </Popconfirm>
      <Popconfirm
        icon={<Icon style={{ marginTop: "4px" }} type="plus-circle" />}
        title={
          <div>
            <Input
              value={milestoneDes}
              onChange={e => setMilestoneDes(e.target.value)}
              placeholder="Enter your milestone brirf description"
              suffix={
                <Tooltip title="to add member to project">
                  <Icon
                    type="info-circle"
                    style={{ color: "rgba(0,0,0,.45)" }}
                  />
                </Tooltip>
              }
            />
            <div style={{ margin: "16px" }}>
              <label htmlFor="storyPoint: ">how many duration long {":"}</label>
              <InputNumber
                id="storyPoint"
                style={{
                  width: "60px"
                }}
                max={40}
                min={1}
                value={howManyDurations}
                defaultValue={howManyDurations}
                onChange={val => setHowManyduration(val)}
              />
            </div>
          </div>
        }
        placement="bottom"
        onConfirm={addMilestoneCallback}
        onCancel={console.log}
        okText="add milestone"
        cancelText=""
      >
        <Button className={Style.button}>add Milestone </Button>
      </Popconfirm>
    </Col>
  );
};

export default AddButtons;
