import { Modal, Button } from "antd";
import { Input, InputNumber, Upload, Icon } from "antd";
import React, { useState, useEffect } from "react";
import { shallowEqual, useDispatch, useSelector } from "react-redux";
import { updateProjectDetail } from "../../../action/project";
import Style from "./style.module.css";

const UpdateProjectModal = props => {
  const [loading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [duration, setDuration] = useState(1);
  const [projectDescription, setDescription] = useState("");
  const [unvalidForm, setUnValidForm] = useState(true);
  const [file, setFile] = useState(null);

  const { projectId, title, description, sprintDuration } = useSelector(
    state => state.projectDetail.projectInfo,
    shallowEqual
  );
  const dispatch = useDispatch();

  const handleOk = () => {
    setLoading(true);
    console.log(projectDescription, name);

    const formData = new FormData();

    formData.append("title", name);
    formData.append("description", projectDescription);
    formData.append("sprintDuration", duration);
    formData.append("projectId", projectId);
    formData.append("logo", file);

    dispatch(updateProjectDetail(formData));

    setLoading(false);
    props.setVisible(false);
  };

  useEffect(() => {
    if (name.length >= 3 && projectDescription.length >= 8) {
      setUnValidForm(false);
    } else {
      setUnValidForm(true);
    }
  }, [name, projectDescription]);

  useEffect(() => {
    setName(title);
    setDescription(description);
    setDuration(sprintDuration);
    return () => {};
    // eslint-disable-next-line
  }, [projectId]);

  const uploadSetting = {
    onRemove: file => {
      setFile(null);
    },
    beforeUpload: file => {
      setFile(state => file);
      return false;
    }
  };

  return (
    <Modal
      visible={props.visible}
      title="Update Project"
      onCancel={() => props.setVisible(false)}
      footer={[
        <Button key="back" onClick={() => props.setVisible(false)}>
          Cancel
        </Button>,
        <Button
          disabled={unvalidForm}
          key="submit"
          type="primary"
          loading={loading}
          onClick={handleOk}
        >
          Create
        </Button>
      ]}
    >
      <div className={`${Style.formControll}`}>
        <label htmlFor="pName">project name :</label>
        <Input
          onChange={e => setName(e.target.value)}
          placeholder="project title min length is 3 char"
          value={name}
          id="pName"
        />
      </div>

      <div className={`${Style.formControll}`}>
        <label htmlFor="pDetail">project detail :</label>
        <Input.TextArea
          id="pDetail"
          value={projectDescription}
          onChange={e => setDescription(e.target.value)}
          placeholder="project description min length is 8 char"
          autosize={{ minRows: 2, maxRows: 6 }}
        />
      </div>

      <div className={`${Style.formControll}`}>
        <label htmlFor="pduration">project duration :</label>
        <br />
        <InputNumber
          id="pduration"
          style={{
            width: "60px",
            marginLeft: "8px"
          }}
          max={15}
          min={1}
          value={duration}
          defaultValue={duration}
          onChange={val => setDuration(val)}
        />
      </div>
      <div className={`${Style.formControll}`}>
        <label htmlFor="pduration">project duration :</label>
        <br />
        <Upload {...uploadSetting} multiple={false} style={{ width: "240px" }}>
          <Button style={{ marginLeft: "8px" }}>
            <Icon type="upload" /> Select New Profile pic{"  "}
          </Button>
        </Upload>
      </div>
    </Modal>
  );
};

export default UpdateProjectModal;
