import React, { useEffect, useState } from "react";
import { Typography, Icon, Avatar } from "antd";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { getProjectsDetail } from "../../../action/project";
import ProjectCreateModal from "./ProjectUpdateModal";
import ProjectAdmin from "../../util/ProjectAdmin";
const { Title, Paragraph } = Typography;
const ProjectDesc = ({ projectId }) => {
  const [visible, setVisible] = useState(false);

  const dispatch = useDispatch();
  const detail = useSelector(state => state.projectDetail, shallowEqual);
  //   const incrementCounter = useCallback(() => dispatch(getMember()), [dispatch]);

  useEffect(() => {
    dispatch(getProjectsDetail(projectId));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [projectId]);
  return detail.projectInfo ? (
    <>
      <ProjectCreateModal visible={visible} setVisible={setVisible} />

      <Typography
        style={{
          margin: "16px"
        }}
      >
        <div></div>
        <Title
          style={{
            margin: " 8px"
          }}
        >
          <Avatar shape="square" size={64} src={detail.projectInfo.logo} />{" "}
          {detail.projectInfo.title}
          <span style={{}}>
            <ProjectAdmin>
              <Icon
                onClick={() => setVisible(true)}
                type="setting"
                style={{
                  fontSize: "16px",
                  marginLeft: "8px",
                  marginTop: "-6px",
                  cursor: "pointer",
                  color: "#999999aa"
                }}
                theme="outlined"
              />
            </ProjectAdmin>
          </span>
        </Title>
        <Paragraph>{detail.projectInfo.description} </Paragraph>
      </Typography>
    </>
  ) : (
    <Title> no info</Title>
  );
};

export default ProjectDesc;
