import React, { useEffect } from "react";

import { Tag, Spin, Icon, Tooltip } from "antd";
import Style from "./style.module.css";
import { getProjectActivitySprint } from "../../../action/project";

import { shallowEqual, useSelector, useDispatch } from "react-redux";

const ProjectSprintActivity = ({ projectId }) => {
  const dispatch = useDispatch();
  const { loading, sprints, activities, milestones } = useSelector(
    state => state.sprintActivity,
    shallowEqual
  );

  useEffect(() => {
    dispatch(getProjectActivitySprint(projectId));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [projectId]);

  return (
    <>
      {!loading ? (
        <>
          <div
            data-name="activities"
            className={Style.tag_flex + " " + Style.borderi + " " + Style.tag}
          >
            {activities.length ? (
              activities.map(act => (
                <Tag color="cyan" key={act.activityId}>
                  {act.activityName}
                </Tag>
              ))
            ) : (
              <p>no activity</p>
            )}
          </div>{" "}
          <div
            data-name="sprint"
            className={Style.tag_flex + " " + Style.borderi + " " + Style.tag}
          >
            {milestones.length || sprints.length ? (
              [...milestones, ...sprints]
                .sort((a, b) => new Date(a.dueDate) - new Date(b.dueDate))
                .map(ms =>
                  ms.sprintName ? (
                    <Tag
                      color="cyan"
                      style={{ margin: "4px" }}
                      key={"sp" + ms.sprintId}
                    >
                      {ms.sprintName}
                    </Tag>
                  ) : (
                    <Tooltip
                      placement="top"
                      title={ms.description}
                      key={"ml" + ms.milestoneId}
                    >
                      <Tag color="red" style={{ margin: "4px" }}>
                        <Icon type="caret-down" />
                      </Tag>
                    </Tooltip>
                  )
                )
            ) : (
              <p>not thing :)))</p>
            )}
          </div>
        </>
      ) : (
        <Spin tip="Loading..." />
      )}
    </>
  );
};

export default ProjectSprintActivity;
