import React, { useEffect } from "react";
import { Skeleton, Avatar, Typography } from "antd";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { getMember } from "../../../action/project";

const { Text } = Typography;

const ProjectMember = ({ projectId }) => {
  const Yar = (id, name, userName, avatar) => (
    <div
      key={id}
      className="ant-card-hoverable"
      style={{
        width: "180px",
        display: "flex",
        backgroundColor: "white",
        padding: "8px",
        justifyContent: "space-around",
        margin: "8px auto",
        borderRadius: "8px"
      }}
    >
      <Avatar src={avatar} size={40} icon="user" />
      <div>
        <h5>{name}</h5>
        <Text type="secondary">{userName}</Text>
      </div>
    </div>
  );

  const WaitYar = (
    <div
      className="ant-card-hoverable"
      style={{
        width: "180px",
        display: "flex",
        backgroundColor: "white",
        padding: "8px",
        justifyContent: "space-around",
        margin: "8px auto",
        borderRadius: "8px"
      }}
    >
      <Skeleton avatar active paragraph={{ rows: 1 }} />,
    </div>
  );

  const dispatch = useDispatch();
  const { loading, members } = useSelector(
    state => state.projectMember,
    shallowEqual
  );
  //   const incrementCounter = useCallback(() => dispatch(getMember()), [dispatch]);

  useEffect(() => {
    dispatch(getMember(projectId));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [projectId]);

  return (
    <div
      style={{
        //
        display: "flex",
        flexWrap: "wrap",
        justifyContent: "space-around",
        padding: "8px",
        border: "1px solid #e0e0e0",
        margin: " auto 0px",
        borderRadius: "8px"
      }}
    >
      {loading ? (
        <>
          {" "}
          {WaitYar}
          {WaitYar}{" "}
        </>
      ) : (
        <></>
      )}

      {members.length ? (
        members.map((member, index) =>
          Yar(
            member.user.userId,
            member.user.name,
            member.user.userName,
            member.user.avatar
          )
        )
      ) : (
        <p>no user</p>
      )}
    </div>
  );
};

export default ProjectMember;
