import {
  Modal,
  Checkbox,
  Button,
  Col,
  Row,
  Icon,
  Input,
  Badge,
  InputNumber,
  Switch,
  Select
} from "antd";

import { shallowEqual, useSelector, useDispatch } from "react-redux";
import Style from "./style.module.css";
import React, { useState, useEffect, useCallback } from "react";
import { addStory, deselectStory, updateStory } from "../../../action/project";
const { Option } = Select;
const CreateModal = props => {
  const [formloading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [who, setWho] = useState("");
  const [what, setWhat] = useState("");
  const [why, setWhy] = useState("");
  const [act, setAct] = useState();
  const [sprint, setSprint] = useState();
  const [unvalidForm, setUnValidForm] = useState(false);
  const [acceptText, setAcceptText] = useState("");
  const [acceptData, setAcceptData] = useState([]);
  const [priority, setPriority] = useState("");
  const [isEpic, setIsEpic] = useState(false);
  const [dependency, setSelectedDependency] = useState([]);
  const [assignment, setAssignment] = useState([]);
  const [storyPoint, setStoryPoint] = useState(1);
  const [storyId, setStoryId] = useState();

  const dispatch = useDispatch();
  const { loading, stories, selectedStory } = useSelector(
    state => state.projectStories,
    shallowEqual
  );

  const deseletStoryCallback = useCallback(
    () => dispatch(deselectStory()),
    // eslint-disable-next-line
    []
  );
  const detail = useSelector(state => state.projectDetail, shallowEqual);
  const auth = useSelector(state => state.auth, shallowEqual);

  const { members } = useSelector(state => state.projectMember, shallowEqual);

  const { activities, sprints } = useSelector(
    state => state.sprintActivity,
    shallowEqual
  );

  const canEditStory = () => {
    if (
      (detail.projectInfo &&
        detail.projectInfo.creatorId === auth.user.userId) ||
      (selectedStory.assignment &&
        selectedStory.assignment.includes(auth.user.userId))
    ) {
      return true;
    } else {
      return false;
    }
  };

  useEffect(() => {
    if (selectedStory.storyId) {
      props.setVisible(true);
      setName(selectedStory.storyName || " ");
      setWho(selectedStory.as || " ");
      setWhat(selectedStory.iWant || " ");
      setWhy(selectedStory.soThat || " ");
      setAcceptData(selectedStory.acceptanceTest || []);
      setStoryPoint(selectedStory.storyPoint);
      setPriority(selectedStory.priority || "");
      setIsEpic(selectedStory.isEpic);
      setAct(selectedStory.activityId);
      setSprint(selectedStory.sprintId);
      setStoryId(selectedStory.storyId);
      setAssignment(selectedStory.assignment);
      setSelectedDependency(selectedStory.dependency);
    }
    // eslint-disable-next-line
  }, [selectedStory]);

  const cancelModal = () => {
    setName("");
    setWho("");
    setWhat("");
    setWhy("");
    setAct();
    setSprint();
    setUnValidForm(false);
    setAcceptText("");
    setAcceptData([]);
    setPriority("");
    setIsEpic(false);
    setSelectedDependency([]);
    setAssignment([]);
    setStoryPoint(1);
    setStoryId(null);
    deseletStoryCallback();
    props.setVisible(false);
  };

  const addAccept = () => {
    if (acceptText.length) {
      const data = {
        test: acceptText,
        checked: false
      };
      setAcceptData(allData => [...allData, data]);
      setAcceptText("");
    }
  };
  const enterPress = e => {
    if (e.keyCode === 13) {
      addAccept();
    }
  };

  const checkButtonClick = item => {
    let newAllData = [...acceptData];
    const itemIndex = newAllData.findIndex(acceptItem => acceptItem === item);
    if (itemIndex !== -1) {
      newAllData[itemIndex].checked = !newAllData[itemIndex].checked;
    }
    setAcceptData(newAllData);
  };

  const deleteButtonClick = item => {
    const newAllData = acceptData.filter(acceptItem => acceptItem !== item);
    setAcceptData(newAllData);
  };

  const handleOk = () => {
    setLoading(true);
    const data = {
      projectId: props.projectId,
      storyName: name,
      as: who,
      iWant: what,
      soThat: why,
      storyPoint: storyPoint,
      priority: priority,
      acceptanceTest: acceptData,
      dependency: dependency,
      isEpic: isEpic,
      activityId: act,
      sprintId: sprint,
      assignment: assignment,
      storyId: storyId
    };
    console.log(data);
    if (storyId) {
      dispatch(updateStory(data));
    } else {
      dispatch(addStory(data));
    }

    setName("");
    setWho("");
    setWhat("");
    setWhy("");
    setAct();
    setSprint();
    setUnValidForm(false);
    setAcceptText("");
    setAcceptData([]);
    setPriority("");
    setIsEpic(false);
    setSelectedDependency([]);
    setAssignment([]);
    setStoryPoint(1);
    setStoryId(null);

    deseletStoryCallback();

    setLoading(false);
    props.setVisible(false);
  };

  useEffect(() => {
    if (
      who.length >= 2 &&
      what.length >= 2 &&
      why.length >= 2 &&
      name.length >= 2
    ) {
      setUnValidForm(false);
    } else {
      setUnValidForm(true);
    }
  }, [who, what, why, name, props.projectId]);

  return (
    <Modal
      className={Style.modalContainer}
      bodyStyle={{
        minWidth: "300px"
      }}
      visible={props.visible}
      // title="Create Project"
      onCancel={cancelModal}
      footer={[
        <Button key="back" onClick={() => cancelModal()}>
          Cancel
        </Button>,
        canEditStory() &&
          (storyId ? (
            <Button
              disabled={unvalidForm}
              key="submit"
              type="primary"
              loading={formloading}
              onClick={handleOk}
            >
              Update User Story
            </Button>
          ) : (
            <Button
              disabled={unvalidForm}
              key="submit"
              type="primary"
              loading={formloading}
              onClick={handleOk}
            >
              Create User Story
            </Button>
          ))
      ]}
    >
      <div style={{ marginBottom: "16px" }}> </div>
      <Row
        style={{
          justifyContent: "space-between",
          display: "flex",
          flexWrap: "wrap"
        }}
      >
        <Col md={14} xs={24} style={{ marginTop: "8px" }}>
          <div className={`${Style.formControll}`}>
            <label htmlFor="name">name:</label>
            <Input
              onChange={e => setName(e.target.value)}
              placeholder="name of user story"
              id="name"
              value={name}
            />
          </div>
          <div className={`${Style.formControll}`}>
            <label htmlFor="who">as :</label>
            <Input
              onChange={e => setWho(e.target.value)}
              placeholder="who want to do this"
              id="who"
              value={who}
            />
          </div>
          <div className={`${Style.formControll}`}>
            <label htmlFor="what">i want:</label>
            <Input
              onChange={e => setWhat(e.target.value)}
              placeholder="what you want to do"
              id="what"
              value={what}
            />
          </div>
          <div className={`${Style.formControll}`}>
            <label htmlFor="why">so that:</label>
            <Input.TextArea
              value={why}
              onChange={e => setWhy(e.target.value)}
              placeholder="why you want to do this"
              autosize={{ minRows: 2, maxRows: 6 }}
              id="why"
            />
          </div>
          <div className={`${Style.formControll}`}>
            <label htmlFor="which">acceptance criteria:</label>
            <Input
              placeholder="by which condition do this"
              id="which"
              onKeyDown={enterPress}
              value={acceptText}
              onChange={e => setAcceptText(e.target.value)}
              suffix={
                <Icon
                  type="plus-circle"
                  onClick={addAccept}
                  style={{ color: "rgba(0,0,0,.45)" }}
                />
              }
            />
          </div>
          {acceptData.length ? (
            <Row>
              {acceptData.map((item, index) => (
                <Col
                  key={index}
                  span={24}
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    marginBottom: "4px",
                    marginTop: "4px"
                  }}
                >
                  <Checkbox
                    style={{
                      textDecoration: item.checked ? "line-through" : "none",
                      color: item.checked ? "gray" : "black"
                    }}
                    onChange={() => checkButtonClick(item)}
                    checked={item.checked ? true : false}
                  >
                    {item.test}
                  </Checkbox>
                  <Icon type="delete" onClick={() => deleteButtonClick(item)} />
                </Col>
              ))}
            </Row>
          ) : (
            <></>
          )}
        </Col>
        <Col md={8} xs={16} style={{ margin: "0 auto" }}>
          <div style={{ margin: "16px" }}>
            <label htmlFor="storyPoint: ">story point {"   "}</label>
            <InputNumber
              id="storyPoint"
              style={{
                width: "60px"
              }}
              max={40}
              min={1}
              value={storyPoint}
              defaultValue={storyPoint}
              onChange={setStoryPoint}
            />
          </div>
          <div style={{ margin: "16px" }}>
            <Select
              value={priority || "Could"}
              // placeholder="select project priorty"
              style={{ width: "100%" }}
              onChange={value => setPriority(value)}
            >
              <Option value="disabled" disabled>
                Select Priority
              </Option>
              <Option value="Could">
                {" "}
                <Badge status="success" text="Could" />
              </Option>
              <Option value="Should">
                <Badge status="default" text="Should" />
              </Option>
              <Option value="Must">
                <Badge status="error" text="Must" />
              </Option>
            </Select>
          </div>{" "}
          <div style={{ margin: "16px" }}>
            <Select
              placeholder="select activty"
              style={{ width: "100%" }}
              value={act}
              onChange={value => setAct(value)}
            >
              <Option value="disabled" disabled>
                selecte activity
              </Option>

              {activities.map(activity => (
                <Option value={activity.activityId} key={activity.activityId}>
                  {" "}
                  <Badge status="default" text={activity.activityName} />
                </Option>
              ))}
            </Select>
          </div>{" "}
          <div style={{ margin: "16px" }}>
            <Select
              placeholder="select sprint"
              style={{ width: "100%" }}
              value={sprint}
              onChange={value => setSprint(value)}
            >
              <Option value="disabled" disabled>
                selecte sprint
              </Option>

              {sprints.map(spr => (
                <Option value={spr.sprintId} key={spr.sprintId}>
                  {" "}
                  <Badge status="default" text={spr.sprintName} />
                </Option>
              ))}
            </Select>
          </div>{" "}
          <div style={{ margin: "16px" }}>
            <h4 style={{ float: "left", marginRight: "16px" }}>Is Epic:</h4>
            <Switch
              checked={isEpic}
              onChange={(checked, e) => setIsEpic(checked)}
              checkedChildren={<Icon type="check" />}
              unCheckedChildren={<Icon type="close" />}
              defaultChecked
            />
          </div>{" "}
          <div style={{ margin: "16px" }}>
            <Select
              loading={loading}
              mode="multiple"
              placeholder="select prerequisite of project"
              onChange={value => setSelectedDependency(value)}
              style={{ width: "100%" }}
              value={dependency}
            >
              {stories
                .filter(o => !dependency.includes(o.storyId))
                .map(item => (
                  <Option key={item.storyId} value={item.storyId}>
                    <Badge status="default" text={item.storyName} />
                  </Option>
                ))}
            </Select>
          </div>
          <div style={{ margin: "16px" }}>
            <Select
              // loading={loading}
              mode="multiple"
              placeholder="assign to members"
              onChange={value => setAssignment(value)}
              style={{ width: "100%" }}
              value={assignment}
            >
              {members
                .filter(o => !assignment.includes(o.user.userId))
                .map(item => (
                  <Option key={item.user.userId} value={item.user.userId}>
                    <Badge status="default" text={item.user.name} />
                  </Option>
                ))}
            </Select>
          </div>
        </Col>
      </Row>
    </Modal>
  );
};

export default CreateModal;
