import React, { useEffect } from "react";

import { Skeleton, Badge } from "antd";
import Style from "./style.module.css";
import { shallowEqual, useSelector, useDispatch } from "react-redux";
import { getStories, selectStory } from "../../../action/project";

const ProjectStories = ({ projectId }) => {

    const dispatch = useDispatch();
    const { loading, stories } = useSelector(
        state => state.projectStories,
        shallowEqual
    );

    useEffect(() => {
        dispatch(getStories(projectId));
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [projectId]);

    return (
        <>
            {
                loading ?
                    (
                        <>
                            <div className={`${Style.usBox} `}>
                                <div
                                    style={{
                                        height: "120px",
                                        overflow: "hidden",
                                        width: "100%",
                                        margin: "auto 0"
                                    }}
                                >
                                    <Skeleton active />
                                </div>
                            </div>
                        </>)
                    :
                    stories.map(story => (
                        <div key={story.storyId} onClick={() => dispatch(selectStory(story))} className={`${Style.usBox} ${Style.hoverCard}`}>
                            <div className={Style.usBoxRow}>
                                <div>{story.storyName} </div>
                                <div>
                                    {" "}
                                    <Badge status="default" text={`${story.as} `} />
                                </div>
                            </div>
                            <div className={Style.usBoxRow}>
                                <div>
                                    {`is epic: ${story.isEpic} `}
                                </div>
                                <div>
                                    <Badge status="default" text={story.status} />
                                </div>
                            </div>
                        </div>
                    )
                    )
            }
        </>
    )
}

export default ProjectStories
